.btn {
  position: relative;
  padding: 12.5px 14px;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 600;
  text-align: center;
  cursor: pointer;
  /*&:focus {
  	box-shadow: 0 0 0 4px $pos-color;
  }*/
}
.btn.btn-all {
  width: 100%;
}
.btn.btn-cursor-default {
  cursor: default;
}
.btn.btn-focus:focus {
  background-color: rgba(225, 225, 225, 0.54);
}
.btn.btn-no-focus:focus {
  -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
          box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
}
